export default {
	data() {
		return {
			resendTime: 60,// 倒计时秒数
			btnList:['答案1','答案2','答案3','答案4','答案5'],
			currentTab: -1,
		};
	},
	onShow(){
		// 计时器
		const timer = setInterval(() => {
			this.resendTime = this.resendTime - 1;
			if (this.resendTime === 0) {
				clearInterval(timer);
			}
		}, 1000);
	},
	methods:{
		clickTab(index){
			this.currentTab = index
		}
	}
}
